const { __ } = wp.i18n;
const { select } = wp.data;

const SelectDemo = prop => {
  const blockCount = select("core/editor").getBlockCount();

  return (
    <div>
      <pre>
        <code>{"select()"}</code>
      </pre>
      <p>
        {__("Block Count:", "jsforwpadvblocks")} {blockCount}
      </p>
    </div>
  );
};

export default SelectDemo;



